	module.exports = {"data":{"post":{"html":"<p>The polls haven't yet closed in France for the referendum on the EU Constitution, but Spiegel Online says <a href=\"http://www.spiegel.de/politik/ausland/0,1518,358084,00.html\" title=\"EU-Referendum: Hohe Wahlbeteiligung in Frankreich - Politik - SPIEGEL ONLINE - Nachrichten\">High Turnout in France</a> and that while the opponents had been leading the polls, the lead has \"melted away\".</p>\n<p>Given Spiegel's record on citing polls in the <a href=\"http://www.papascott.de/archives/2005/02/20/schleswigholstein-votes/\">last</a> <a href=\"http://www.papascott.de/archives/2005/05/22/spd-loses-big-in-nrw/\">two</a> German state elections, I take it that means the constitution will decisively lose. I've decided that's probably a good thing. Europe needs to catch its breath.</p>\n<p><strong>Update 22:04:</strong> <a href=\"http://fistfulofeuros.net/archives/001410.php\" title=\"A Fistful of Euros: And It's A 'No'\">Told you so!</a></p>","fields":{"title":"Wishful Thinking","date":"29 May 2005","url":"https://papascott.de/2005-05-29-wishful-thinking/"}}},"pathContext":{"slug":"/2005-05-29-wishful-thinking/","prev":{"fields":{"title":"Harbor Tour","date":"29 May 2005","slug":"/2005-05-29-harbor-tour/"}},"next":{"fields":{"title":"Experience Not Required","date":"02 Jun 2005","slug":"/2005-06-02-experience-not-required/"}}}}

